import { Component } from '@angular/core';
import { Http, Response } from '@angular/http';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  constructor(private http: Http) { }
  title = 'hackathon';
  allDataCalender: any;
  allDataMatches: any;
  allDataRecentMatches: any;
  showDataCalender: Boolean = false;
  showDataMatches: Boolean = false;
  showDataRecentMatches: Boolean = false;

  p: number = 1;

  getSchedule() {
    this.http.get('http://cricapi.com/api/matchCalendar/O7KRyV9KnKXScWhtr7srCpOuOOy1').subscribe(res => {
      this.p = 1;
      this.showDataCalender = true;
      this.showDataMatches = false;
      this.showDataRecentMatches = false;
      const wholeResponse: any = res;
      const jsonResponse = JSON.parse(wholeResponse._body);
      this.allDataCalender = jsonResponse.data;
      //console.log(this.allData.data);
    });
  }

  getAllMatches() {
    this.http.get('http://cricapi.com/api/matches/O7KRyV9KnKXScWhtr7srCpOuOOy1').subscribe(res => {
      this.showDataMatches = true;
      this.showDataCalender = false;
      this.showDataRecentMatches = false;
      const wholeResponse: any = res;
      const jsonResponse = JSON.parse(wholeResponse._body);
      this.allDataMatches = jsonResponse.matches.slice(0,8);
      this.allDataMatches.map( element => {
        element.firstTeam = Object.values(element)[3];
        element.secondTeam = Object.values(element)[4];
        // element.dateTimeForMatch = 
      });
      console.log(this.allDataMatches);
    });
  }

  getOldMatches() {
    this.http.get('http://cricapi.com/api/cricket/O7KRyV9KnKXScWhtr7srCpOuOOy1').subscribe(res => {
      this.showDataCalender = false;
      this.showDataMatches = false;
      this.showDataRecentMatches = true;
      const wholeResponse: any = res;
      const jsonResponse = JSON.parse(wholeResponse._body);
      this.allDataRecentMatches = jsonResponse.data;
      this.allDataRecentMatches.forEach(element=> {
        element.description.replace("&amp;", "");
      })
      console.log(this.allDataRecentMatches);
    });
  }

  getCommentoryData() {

  }

  liveScore() {
    this.http.get('http://mapps.cricbuzz.com/cbzios/match/livematches').subscribe(res=> {
      console.log(res);
    })
  }
}
